@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #000;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #fafbff;
}

.dashboard {
  width: 100%;
  height: 1198px;
  background: #fafbff;
  display: flex;
  flex-direction: row;
  gap: 71px;
}

.logo {
  display: flex;
  flex-direction: row;
  gap: 8px;
}
.logo__title {
  font-weight: 600;
  font-size: 26px;
  letter-spacing: 0.01em;
  position: relative;
  left: 45px;
}
.logo__title::after {
  content: "v.01";
  position: relative;
  left: 4px;
  font-weight: 500;
  font-size: 10px;
  letter-spacing: -0.01em;
  color: #838383;
}
.logo__img {
  position: absolute;
  transition: 0.3s ease;
}

.sidebar {
  width: 306px;
  height: 100%;
  padding-left: 28px;
  box-shadow: 0 10px 60px 0 rgba(226, 236, 249, 0.5);
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 50;
  transition: 0.3s ease;
}
.sidebar__header {
  display: flex;
  flex-direction: column;
  gap: 52px;
}
.sidebar__logo {
  margin-top: 37px;
}
.sidebar__open {
  display: none;
}
.sidebar__menu {
  display: flex;
  flex-direction: column;
  gap: 18px;
  list-style: none;
}
.sidebar__item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 11px 8px 11px 11px;
  border-radius: 8px;
  width: 250px;
  height: 46px;
}
.sidebar__icon-link {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 14px;
}
.sidebar__link {
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #9197b3;
  text-decoration: none;
}
.sidebar__user {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 12px;
  margin-bottom: 76px;
}
.sidebar__name {
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 0.01em;
}
.sidebar__role {
  font-weight: 400;
  font-size: 12px;
  letter-spacing: 0.01em;
  color: #757575;
  margin-top: -1px;
}

.header {
  margin-top: 41px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.header__title {
  font-weight: 500;
  font-size: 24px;
  color: #000;
}
.header__hand {
  position: relative;
  top: 2px;
}
.header__icon-wrapper::after {
  content: ",";
}

.dashboard__content {
  display: flex;
  flex-direction: column;
  gap: 52px;
}
.dashboard__mobile-menu {
  display: none;
}
.dashboard__main {
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding: 30px 44px 40px 38px;
  width: 968px;
  border-radius: 30px;
  background-color: #fff;
  box-shadow: 0 10px 60px 0 rgba(226, 236, 249, 0.5);
}
.dashboard__main-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 30px;
}
.dashboard__main-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.dashboard__text {
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.dashboard__main-title {
  font-weight: 600;
  font-size: 22px;
  letter-spacing: -0.01em;
  color: #000;
}
.dashboard__main-link {
  font-weight: 400;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #16c098;
  text-decoration: none;
}
.dashboard__search {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 9px;
  padding-left: 8px;
  padding-top: 5px;
  background: #f9fbff;
  border-radius: 10px;
  width: 216px;
  height: 38px;
}
.dashboard__search-input {
  outline: none;
  border: none;
  width: 80%;
  padding-left: 8px;
  background: #f9fbff;
  font-weight: 400;
  font-size: 12px;
  letter-spacing: -0.01em;
  font-family: "Poppins", sans-serif;
}
.dashboard__search-input::placeholder {
  color: #b5b7c0;
}
.dashboard__table {
  width: 100%;
  text-align: left;
  border-collapse: collapse;
  height: 587px;
}
.dashboard__table td {
  padding: 24px 0;
}
.dashboard__table th {
  position: relative;
  padding-bottom: 14px;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #b5b7c0;
}
.dashboard__table td {
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #292d32;
}
.dashboard__table th:nth-child(2),
.dashboard__table td:nth-child(2) {
  position: relative;
  left: 36px;
  width: 120px;
}
.dashboard__table th:nth-child(3),
.dashboard__table td:nth-child(3) {
  position: relative;
  left: 47px;
  width: 180px;
}
.dashboard__table th:nth-child(4),
.dashboard__table td:nth-child(4) {
  position: relative;
  left: 23px;
  width: 220px;
}
.dashboard__table th:nth-child(5),
.dashboard__table td:nth-child(5) {
  position: relative;
  left: 10px;
  width: 150px;
}
.dashboard__table th:nth-child(6) {
  position: relative;
  left: 12.5px;
}
.dashboard__table td:nth-child(6) {
  padding: 0;
  width: 80px;
}
.dashboard__table--active {
  background: rgba(22, 192, 152, 0.38);
  border: 1px solid #00b087;
  border-radius: 4px;
  padding: 3px 17px;
  width: 100%;
  height: 29px;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #008767;
}
.dashboard__table--inactive {
  background: #ffc5c5;
  border: 1px solid #df0404;
  border-radius: 4px;
  padding: 3px 11px;
  width: 100%;
  height: 29px;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #df0404;
}
.dashboard__table thead tr::after {
  content: "";
  position: absolute;
  left: -38px;
  bottom: 0;
  height: 1px;
  width: calc(100% + 44px + 38px);
  background-color: #eee;
}
.dashboard__table tr {
  position: relative;
}
.dashboard__table tbody tr::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: #eee;
}
.dashboard__footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.dashboard__data {
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #b5b7c0;
}
.dashboard__footer-list {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 12px;
  list-style: none;
}
.dashboard__footer-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5f5f5;
  border: 1px solid #eee;
  border-radius: 4px;
  padding: 6px 9px;
  width: 25px;
  height: 24px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 100%;
  letter-spacing: -0.01em;
  color: #404b52;
}
.dashboard__footer-dots {
  font-weight: 500;
  font-size: 12px;
  line-height: 100%;
  letter-spacing: -0.01em;
  color: #000;
  padding-top: 4px;
}

@media (hover: hover) {
  .sidebar__item:hover {
    background-color: #5932ea;
  }
  .sidebar__item:hover .sidebar__link {
    color: #fff;
  }
  .sidebar__item:hover .sidebar__icon-stroke path {
    stroke: #fff;
  }
  .sidebar__item:hover .sidebar__icon-fill path {
    fill: #fff;
  }
  .sidebar__item:hover .sidebar__icon .stroke {
    stroke: #fff;
  }
  .sidebar__item:hover .sidebar__icon .fill {
    fill: #fff;
  }
  .sidebar__item:hover .sidebar__arrow path {
    stroke: #fff;
  }
  .dashboard__footer-button:hover {
    background: #5932ea;
    color: #fff;
  }
}
@media (hover: none) {
  .sidebar__item:active {
    background-color: #5932ea;
  }
  .sidebar__item:active .sidebar__link {
    color: #fff;
  }
  .sidebar__item:active .sidebar__icon-stroke path {
    stroke: #fff;
  }
  .sidebar__item:active .sidebar__icon-fill path {
    fill: #fff;
  }
  .sidebar__item:active .sidebar__icon .stroke {
    stroke: #fff;
  }
  .sidebar__item:active .sidebar__icon .fill {
    fill: #fff;
  }
  .sidebar__item:active .sidebar__arrow path {
    stroke: #fff;
  }
  .dashboard__footer-button:active {
    background: #5932ea;
    color: #fff;
  }
}
@media (min-width: 1441px) {
  .dashboard {
    gap: 0px;
  }
  .dashboard__content {
    margin: 0 auto;
  }
}
@media (max-width: 1400px) {
  .dashboard {
    gap: 0px;
  }
  .dashboard__content {
    margin: 0 auto;
    position: relative;
    left: 50px;
  }
  .sidebar {
    position: absolute;
    height: 1198px;
    z-index: 1;
  }
  .sidebar__open {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: -12.5px;
    top: 43px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    box-shadow: 0 10px 60px 0 rgb(226, 236, 249);
    background: #5932ea;
    font-size: 18px;
    color: #fff;
  }
  .sidebar__open.toggle {
    transform: scaleX(-1);
  }
  .sidebar.toggle {
    min-width: 100px;
    width: 100px;
  }
  .sidebar.toggle.toggle .sidebar__item {
    width: 46px;
  }
  .sidebar.toggle.toggle .sidebar__link {
    display: none;
  }
  .sidebar.toggle.toggle .sidebar__arrow {
    display: none;
  }
  .sidebar.toggle.toggle .sidebar__name,
  .sidebar.toggle.toggle .sidebar__role {
    display: none;
  }
  .sidebar.toggle.toggle .logo__title {
    opacity: 0;
  }
  .sidebar.toggle.toggle .logo__img {
    margin-left: 4px;
  }
  .modal-open::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Полупрозрачный черный фон */
    z-index: 1; /* Помещаем под все остальные элементы */
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.5);
  }
}
@media (max-width: 1150px) {
  .dashboard__mobile-menu {
    display: block;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .dashboard__mobile-menu svg {
    width: 60px;
  }
  .dashboard__content {
    margin: 0 auto;
    position: static;
  }
  .dashboard__header {
    margin-left: 38px;
  }
  .sidebar {
    display: none;
  }
  .sidebar__open {
    display: none;
  }
  .mobile {
    display: flex;
  }
}
@media (max-width: 980px) {
  .dashboard {
    width: 100%;
  }
  .dashboard__content {
    margin: 0 auto;
    width: 100%;
  }
  .dashboard__header {
    margin-left: 38px;
    margin-right: 38px;
  }
  .dashboard__main {
    width: 100%;
  }
  .dashboard__table-wrapper {
    width: 100%;
    overflow-x: scroll;
  }
  .dashboard__table {
    min-width: 900px;
  }
  .dashboard__data {
    font-size: 10px;
  }
}
@media (max-width: 480px) {
  .dashboard__main {
    padding-left: 15px;
    padding-right: 15px;
  }
  .dashboard__data {
    font-size: 8px;
  }
  .dashboard__footer-list {
    gap: 5px;
  }
  .dashboard__footer-button {
    width: 20px;
    height: 20px;
    font-size: 10px;
  }
  .sidebar {
    min-width: 200px;
    width: 200px;
    padding-left: 10px;
  }
  .sidebar__item {
    width: 170px;
    padding-right: 20px;
  }
  .logo {
    align-items: center;
  }
  .logo__title {
    font-size: 18px;
  }
  .logo__img {
    margin-left: 4px;
  }
}